package com.lavsystems.goodluckbingo.pages.login.service

import com.lavsystems.goodluckbingo.api.models.ApiResponse
import com.lavsystems.goodluckbingo.components.ui.Strings.Login.API.GET_USER_BY_CODE
import com.lavsystems.goodluckbingo.domain.models.User
import com.varabyte.kobweb.browser.api
import kotlinx.browser.window
import kotlinx.serialization.json.Json

object UserService {
    suspend fun getUserByCode(code: String): User? {
        return try {
            val result = window.api.tryGet(apiPath = GET_USER_BY_CODE.replace("{code}", code))
            println(result)
            val response = Json.decodeFromString<ApiResponse<User?>>(result.toString())
            if(response is ApiResponse.Success){
                response.data
            } else {
                null
            }
        } catch (e: Exception) {
            println(e.message)
            null
        }
    }
}